/*---------------------------------------------------------------------------------------------
*
*	Flexible Content
*
*---------------------------------------------------------------------------------------------*/

.acf-field-setting-fc_layout {

	// meta
	.acf-fc-meta {
		margin: 0 0 10px;
		padding: 0;

		li {
			margin: 0 0 10px;
			padding: 0;
		}

		.acf-fc-meta-display {
			float: left;
			width: 100%;
			padding-right: 10px;
		}

		.acf-fc-meta-min {
			width: calc(50% - 4px);
			float: left;
			clear: left;
			margin: {
				right: 4px;
			};
		}

		.acf-fc-meta-max {
			width: calc(50% - 4px);
			float: left;
			margin: {
				left: 4px;
			};
		}

		.acf-fc-meta-label .acf-input-prepend,
		.acf-fc-meta-name .acf-input-prepend,
		.acf-fc-meta-display .acf-input-prepend {
			min-width: 60px;
		}
	}

	// actions
	.acf-fl-actions {
		visibility: hidden;

		.reorder-layout {
			cursor: move;
		}

		a {
			padding: 1px 0;
			font-size: 13px;
			line-height: 20px;
		}
	}

	// hover
	&:hover, &.-hover {
		 .acf-fl-actions {
		 	visibility: visible;
		 }
	}
}


/*---------------------------------------------------------------------------------------------
*
*	Clone
*
*---------------------------------------------------------------------------------------------*/

.acf-field-object-clone {

	/* group */
	&[data-display="group"] {


	}


	/* seamless */
	&[data-display="seamless"] {

		.acf-field-setting-instructions,
		.acf-field-setting-layout,
		.acf-field-setting-wrapper,
		.acf-field-setting-conditional_logic {
			display: none;
		}

	}

}